<template>
  <div class="text-left bg-white ">

    <div>
      <div class="text-left bg-slate-100 p-8">

        <h2 class="text-3xl pb-4 font-bold">Search for Images</h2>

        <div>Using the <strong>Date Filter</strong> allows you to complete a more targeted search. Select
          the Date Filter checkbox, move the sliders to the desired range and then type into one of the search boxes below
          and click submit. </div>

        <div class="mt-2 mr-2 flex rounded-md pb-4 pt-4">
          <div class="relative flex flex-wrap flex-grow items-stretch focus-within:z-10">
            <div class="flex-1 ">
              <form role="form">
                <div class="mb-2">
                  <label for="searchItem"
                    class="text-md font-bold tracking-tight text-gray-900 sm:text-xl text-left mb-6 mt-4 pb-2">Search
                    by Producer/Printmakers</label>
                  </div>
                <input type="text" required name="search-item" id="searchItem" v-model="producer_search_item"
                  class="block w-96 border-0 py-2.5 pl-5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6 invalid:[&:not(:placeholder-shown):not(:focus)]:border-red-500"
                  placeholder="Please enter your producer..." />
                <div class="mt-2">
                  <input type="submit" @click="handleProducerSearchRequests" @keydown.enter="handleProducerSearchRequests"
                    class="bg-slate-500 hover:bg-slate-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" value="Submit" />
                </div>
                <div v-if="inProdList">
                  <p class="text-red-600 mt-2">Please use the 'Search for specific Producer/Printmaker' form below.</p>
                </div>
              </form>
            </div>
            <div class="flex-1">
              <form role="form">
                <div class="mb-2">
                  <label for="searchItem"
                    class="text-md font-bold tracking-tight text-gray-900 sm:text-xl text-left mb-6 mt-4 pb-2">Search
                    by Event</label>
                  </div>
                <input type="text" required name="search-item" id="searchItem" v-model="event_search_item"
                  class="block w-96 border-0 py-2.5 pl-5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6 invalid:[&:not(:placeholder-shown):not(:focus)]:border-red-500"
                  placeholder="Please enter your event..." />
                <div class="mt-2">
                  <input type="submit" @click="handleEventSearchRequests" @keydown.enter="handleEventSearchRequests"
                    class="bg-slate-500 hover:bg-slate-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" value="Submit" />
                </div>
              </form>
            </div>

          
          </div>
        </div>
        <div class="pt-4 pb-4">
          <input @change="checkboxToggle" type="checkbox" id="dateFilter" class="mr-4" v-model="dateCheckbox"/> <label for="dateFilter"
            class="text-md">Apply <strong>Date Filter</strong> to search</label>
        </div>

        <!-- date slider -->

        <div v-show="showDateFilter" class="text-left bg-slate-100 pt-6 pb-6">
          <h3 class="text-2xl pb-4 font-bold">Filter by Date</h3>

          <p class="text-md mb-4 pb-6">You can choose to restrict the number of search results returned by selecting a
            date range. Please move the slider handles to the desired points on the date scale.</p>

          <Slider class="mt-12" v-model="dateSlider.value" v-bind="dateSlider" :min="dateSlider.min"
            :max="dateSlider.max"> </Slider>
          <button @click="resetDateSlider"
            class="bg-slate-500 hover:bg-slate-600 text-white font-bold py-2 px-4 mt-4 rounded focus:outline-none focus:shadow-outline"
            type="button">
            Reset slider
          </button>
        </div>

        <!-- subject search and person search (no date filter) -->

        <div class="border-top border-solid mt-6">
              <p class="font-bold">NB: Subject and person searches are not affected by the Date Filter.</p>
        </div>
          

        <div class="mt-2 mr-2 flex rounded-md pb-4 pt-4 mb-4 ">
          <div class="relative flex flex-wrap flex-grow items-stretch focus-within:z-10 ">

           
            <div class="flex-1">
              <form role="form">
                <div class="mb-2">
                  <label for="searchItem"
                    class="text-md font-bold tracking-tight text-gray-900 sm:text-xl text-left mb-6 mt-4 pb-2">Search
                    by Subject</label>
                  </div>
                <input type="text" required name="search-item" id="searchItem" v-model="subject_search_item"
                  class="block w-96 border-0 py-2.5 pl-5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6 invalid:[&:not(:placeholder-shown):not(:focus)]:border-red-500"
                  placeholder="Please enter your subject..." />
                <div class="mt-2">
                  <input type="submit" @click="handleSubjectSearchRequests" @keydown.enter="handleSubjectSearchRequests"
                    class="bg-slate-500 hover:bg-slate-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" value="Submit" />
                </div>
              </form>
            </div>
            <div class="flex-1">
              <form role="form">
                <div class="mb-2">
                  <label for="searchItem"
                    class="text-md font-bold tracking-tight text-gray-900 sm:text-xl text-left mb-6 mt-4 pb-2">Search
                    by Person</label>
                </div>

                <input type="text" required name="search-item" id="searchItem" v-model="person_search_item"
                  class="block w-96 border-0 py-2.5 pl-5 text-gray-900 ring-1 ring-inset ring-gray-300 placeholder:text-gray-400 focus:ring-2 focus:ring-inset focus:ring-indigo-600 sm:text-sm sm:leading-6 invalid:[&:not(:placeholder-shown):not(:focus)]:border-red-500"
                  placeholder="Please enter your person..." />
                <div class="mt-2">
                  <input type="submit" @click="handlePersonSearchRequests" @keydown.enter="handlePersonSearchRequests"
                    class="bg-slate-500 hover:bg-slate-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline" value="Submit" />  
                </div>
              </form>
            </div>

         </div>
        </div>

        <div class="pb-6"
          v-if="(search_event_data.length !== 0 || search_subject_index_data.length !== 0 || search_subject_data.length !== 0 || search_person_data.length !== 0 || search_producer_data.length !== 0) && resultShow === true">
          <span class="ml-4 pl-4 justify-center relative w-full">
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5"
              stroke="currentColor" class="w-6 h-6">
              <path stroke-linecap="round" stroke-linejoin="round" d="M11.25 4.5l7.5 7.5-7.5 7.5m-6-15l7.5 7.5-7.5 7.5" />
            </svg>
            <a href="#" @click="viewResultsPage" class="text-xl absolute top-4 left-5 w-52 pt-2">VIEW RESULTS PAGE</a></span>
        </div>

        <div class="pb-6"
          v-if="(search_event_data.length === 0 && search_subject_index_data.length === 0 && search_subject_data.length === 0 && search_person_data.length === 0 && search_producer_data.length === 0) && resultShow === true">
          <span class="ml-4 pl-4 justify-center relative w-full">
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="1.5"
              stroke="currentColor" class="w-6 h-6">
              <path stroke-linecap="round" stroke-linejoin="round" d="M11.25 4.5l7.5 7.5-7.5 7.5m-6-15l7.5 7.5-7.5 7.5" />
            </svg>
            <a href='#' class="text-xl absolute top-4 left-5 w-52 pt-2">NO RESULTS FOUND</a></span>
        </div>

        <!-- spinner -->

        <div v-if="spinnerShow" class="pt-4 pb-4">
          <FwbSpinner v-if="spinnerShow" size="10" color="blue" />
        </div>

         <!-- clear search button -->
        <div>
          <button type="button" @click="clearSearches" class="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 mt-4 rounded focus:outline-none focus:shadow-outline">Clear</button>
        </div>

        <!-- select option based search for popular printmakers/artists -->

        <div class="mt-2 mr-2 flex rounded-md pb-4 pt-4 mb-4">
          <div class="relative flex flex-wrap flex-grow items-stretch focus-within:z-10">
            <div class="flex-1">
              <form>
                <fieldset class="border-solid border-2 border-slate-600 rounded p-4 mt-12">
                  <h4 class="font-bold text-xl">Search for specific Producer/Printmakers</h4>
                  <div class="pt-2 pb-2">If you are searching for producers : <strong>Barlow</strong>,
                    <strong>Gaywood</strong>, <strong>Hollar</strong>, <strong>Kneller</strong>, or <strong>Lely</strong>,
                    click the button below.</div>
                  <div id="selectPP" class="pb-4 pt-4 inline-block relative w-64">
                    <select v-model="producer_name"
                      class="block appearance-none w-full bg-white border border-gray-400 hover:border-gray-500 px-4 py-2 pr-8 rounded shadow leading-tight focus:outline-none focus:shadow-outline mb-6">
                      <option>Barlow</option>
                      <option>Gaywood</option>
                      <option>Hollar</option>
                      <option>Kneller</option>
                      <option>Lely</option>
                    </select>
                    <button @click="viewProducerPage"
                      class="bg-slate-500 hover:bg-slate-600 text-white font-bold py-2 px-4 rounded focus:outline-none focus:shadow-outline"
                      type="button">
                      Find records
                    </button>
                  </div>
                </fieldset>
              </form>
            </div>
          </div>
        </div>
      </div>
 
    </div>
  </div>
</template>

<script>


import Slider from '@vueform/slider'
import axios from 'axios'
import router from '../router'
import * as dataService from '../services/dataService';
import { FwbSpinner } from 'flowbite-vue'


export default {
  components: {
    Slider,
    FwbSpinner
  },
  data: () => ({
    dateSlider: {
      value: [1600, 1700],
      min: 1550,
      max: 1815
    },
    subject_search_item: '',
    event_search_item: '',
    person_search_item: '',
    producer_search_item: '',
    producer_name : '',
    search_subject_data: [],
    search_subject_index_data: [],
    search_event_data: [],
    search_person_data: [],
    search_producer_data: [],
    all_images: [],
    prodList: ['barlow', 'gaywood', 'hollar', 'kneller', 'lely'],
    inProdList: false,
    showDateFilter: false,
    spinnerShow: false,
    resultShow: false,
    dateCheckbox: null
  }), 
  methods: {
    viewResultsPage() {
      router.push('/resultsView')
    },
    viewProducerPage() {
      router.push({
        name: 'Producer',
        params: {
          producerName: this.producer_name
        }
      })
    },
    setShow() {
      setTimeout(() => {
        this.resultShow = true;
        this.spinnerShow = false;
      }, 3000);
    },
    clearSearches() {
      localStorage.clear();
      this.subject_search_item = '';
      this.event_search_item = '';
      this.person_search_item = '';
      this.producer_search_item = '';
      this.search_subject_data = [];
      this.search_subject_index_data = [];
      this.search_event_data = [];
      this.search_person_data = [];
      this.search_producer_data = [];
      this.resultShow = false;
      this.showDateFilter = false;
      this.dateCheckbox = null;
    },
    handleSubjectSearchRequests(e) {
      e.preventDefault();
      // reset between search requests
      this.search_subject_data = [];
      this.search_subject_index_data = [];
      this.removeMessage();
      this.resultShow = false;
      // start the spinner
      this.spinnerShow = true;
      this.getImagesBySubject(this.subject_search_item);
      this.getImagesBySubjectIndex(this.subject_search_item);
      // add a timeout so that data has a chance to load into localstorage
      this.setShow()
    },
    handleEventSearchRequests(e) {
      e.preventDefault();
      // reset between search requests
      this.search_event_data = [];
      this.removeMessage();
      this.resultShow = false;
      this.spinnerShow = true;
      if (this.showDateFilter === true) {
        this.getImagesByEventDate(this.event_search_item, this.dateSlider.value);
      }
      else {
        this.getImagesByEvent(this.event_search_item);
      }
      this.setShow()
    },
    handlePersonSearchRequests(e) {
      e.preventDefault();
      // reset between search requests
      this.search_person_data = [];
      this.removeMessage();
      this.resultShow = false;
      this.spinnerShow = true;
      this.getImagesByPerson(this.person_search_item);
      this.getImagesByLabelSearch(this.person_search_item);
      this.setShow()
    },
    handleProducerSearchRequests(e) {
      e.preventDefault();
      // reset between search requests
      this.search_producer_data = [];
      this.resultShow = false;
      if (this.checkInProdList(this.producer_search_item.toLowerCase())) {
        this.inProdList = true;
      }
      else {
        this.spinnerShow = true;
        this.removeMessage();
        if (this.showDateFilter === true) {
          this.getImagesByProducerDate(this.producer_search_item, this.dateSlider.value);
        }
        else {
          this.getImagesByProducer(this.producer_search_item);
        }
        this.setShow();
      }
    },
    checkInProdList(item) {
      // search in the prodList
      if (this.prodList.indexOf(item) !== -1) {
        return true;
      }
    },
    checkboxToggle() {
      if (this.showDateFilter == false) {
        this.showDateFilter = true;
      }
      else if (this.showDateFilter == true) {
        this.showDateFilter = false;
      }
    },
    resetDateSlider() {
      this.dateSlider.value = [1600, 1700]
    },
    async getImagesBySubject(subject_search_item) {
      let subject_data_bpis = [];
      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get("http://localhost:3000/api/image_subject_search/" + subject_search_item)
        .then((response) => {

          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No subject data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_subject_data = response.data.recordset;
            this.search_subject_data.forEach(function (object) {
              subject_data_bpis.push(object.BPI_catKey);
            });
          }
          if (subject_data_bpis.length !== 0) {
            this.callDataService(subject_data_bpis);
          }
        })
        .catch(function (err) {
          console.log(err);
        })
    },
    async getImagesBySubjectIndex(subject_search_item) {
      let subject_index_data_bpis = [];
      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get("http://localhost:3000/api/image_subject_search_by_index/" + subject_search_item)
        .then((response) => {

          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No subject data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_subject_index_data = response.data.recordset;
            this.search_subject_index_data.forEach(function (object) {
              if(object.BPI_catKey !== null){
                subject_index_data_bpis.push(object.BPI_catKey);
              }  
            });
          }
          if (subject_index_data_bpis.length !== 0) {
            this.callDataService(subject_index_data_bpis);
          }
        })
        .catch(function (err) {
          console.log(err);
        })
    },
    async getImagesByEventDate(event_search_item, sliderValues) {
      let event_data_bpis = [];
      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get("http://localhost:3000/api/image_event_search_date/" + event_search_item + '/' + sliderValues[0] + '/' + sliderValues[1])
        .then((response) => {

          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No event data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_event_data = response.data.recordset;
            this.search_event_data.forEach(function (object) {
              if(object.BPI_catKey !== null){
                event_data_bpis.push(object.BPI_catKey);
              }  
            });
          }
          if (event_data_bpis.length !== 0) {
            this.callDataService(event_data_bpis);
          }
        })
        .catch(function (err) {
          console.log(err);
        })
    },
    async getImagesByEvent(event_search_item) {
      let event_data_bpis = [];
      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get("http://localhost:3000/api/image_event_search/" + event_search_item)
        .then((response) => {

          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No event data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_event_data = response.data.recordset;
            this.search_event_data.forEach(function (object) {
              if(object.BPI_catKey !== null){
                event_data_bpis.push(object.BPI_catKey);
              }  
            });
          }
          if (event_data_bpis.length !== 0) {
            this.callDataService(event_data_bpis);
          }
        })
        .catch(function (err) {
          console.log(err);
        })
    },
    async getImagesByPerson(person_search_item) {
      let person_data_bpis = [];
      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get("http://localhost:3000/api/image_person_search/" + person_search_item)
        .then((response) => {

          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No person data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_person_data = response.data.recordset;
            this.search_person_data.forEach(function (object) {
              if(object.BPI_catKey !== null){
                person_data_bpis.push(object.BPI_catKey);
              }  
            });
          }
          if (person_data_bpis.length !== 0) {
            this.callDataService(person_data_bpis);
          }
        })
        .catch(function (err) {
          console.log(err);
        })
    },
    async getImagesByLabelSearch(person_search_item) {
      let person_data_bpis = [];
      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get("http://localhost:3000/api/image_label_search/" + person_search_item)
        .then((response) => {

          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No person data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_person_data = response.data.recordset;
            this.search_person_data.forEach(function (object) {
              if(object.BPI_catKey !== null){
                person_data_bpis.push(object.BPI_catKey);
              }  
            });
          }
          if (person_data_bpis.length !== 0) {
            this.callDataService(person_data_bpis);
          }
        })
        .catch(function (err) {
          console.log(err);
        })
    },

    async getImagesByProducerDate(producer_search_item, sliderValues) {
      let producer_data_bpis = [];
      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get("http://localhost:3000/api/image_producer_search_date/" + producer_search_item + '/' + sliderValues[0] + '/' + sliderValues[1])
        .then((response) => {

          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No person data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_producer_data = response.data.recordset;
            this.search_producer_data.forEach(function (object) {
              if(object.BPI_catKey !== null){
                producer_data_bpis.push(object.BPI_catKey);
              }  
            });
          }
          if (producer_data_bpis.length !== 0) {
            const reduced_bpi_list = this.removeDuplicates(producer_data_bpis);
            this.callDataService(reduced_bpi_list);
          }
        })
        .catch(function (err) {
          console.log(err);
        })
    },
    async getImagesByProducer(producer_search_item) {
      let producer_data_bpis = [];
      // use axios to call a stored procedure in the API functions, passing the search_item as 'item'
      axios
        .get("http://localhost:3000/api/image_producer_search/" + producer_search_item)
        .then((response) => {
          if (response.data.recordset === undefined || response.data.recordset.length === 0) {
            console.log('No producer data');
          }
          else {
            // get all the BPI_catKeys numbers of returned data
            this.search_producer_data = response.data.recordset;
            this.search_producer_data.forEach(function (object) {
              if(object.BPI_catKey !== null){
                producer_data_bpis.push(object.BPI_catKey);
              }  
            });
          }
          if (producer_data_bpis.length !== 0) {
            this.callDataService(producer_data_bpis);
          }
        })
        .catch(function (err) {
          console.log(err);
        })
    },
    removeMessage() {
      this.inProdList = false;
    },
    removeDuplicates(array){
      let unique = [];
      array.forEach(element => {
        if(!unique.includes(element)){
          unique.push(element)
        }
      });
      return unique;
    },
    // handle writes to localstorage
    async callDataService(data_bpis){
      localStorage.setItem('image_bpis', data_bpis);
      data_bpis.forEach(async bpi_num => {
        if (bpi_num !== null) {
          await dataService.getAllImageDetails(bpi_num); 
        }
      })
    }
  }
}
</script>

<style src="@vueform/slider/themes/default.css"></style>

<style>.slider-connect,
.slider-tooltip {
  background-color: slategray;
  border-color: slategray;

}</style>